<script>
import Tweet from '@/components/Tweet'
import TweetUser from '@/components/TweetUser'
import ZoneTweet from '@/components/ZoneTweet'

export default {
  name: 'Home',
  data: () => ({
    tweets: []
  }),
  components: {
    ZoneTweet,
    Tweet,
    TweetUser
  },
  created() {
  
    this.$mitt.on('tweet', e => {
      if(this.tweets.length > 0){
        this.tweets.unshift(e)
      }else{
        this.tweets.push(e)
      }
     
    })
  }
}
</script>

<template>
  <div id="home-page" class="home">
    <div class="title">
      <h3>Home</h3>
      <img src="@/assets/img/starsicon.png" alt="" class="icon" />
    </div>
    <ZoneTweet />
    <TweetUser :i="i" v-for="(i, index) in tweets" :key="index" />
    <Tweet />
    <Tweet />
    <Tweet />
    <Tweet />
    <Tweet />
    <Tweet />
    <Tweet />
    <Tweet />
    <Tweet />
    <Tweet />
    <Tweet />
    <Tweet />
    <Tweet />
    <Tweet />
    <Tweet />
    <Tweet />
    <Tweet />
    <Tweet />
    <Tweet />
    <Tweet />
    <Tweet />
    <Tweet />
    <Tweet />
    <Tweet />
    <Tweet />
  </div>
</template>

